/* AboutUs.module.css */
.gridContainer {
  display: grid;
  grid-template-columns: 33% 66%;
  justify-content: center;
  text-align: center;
  align-items: center;
  gap: 2rem;
  margin: 120px auto;
}

.verticalLine {
  position: absolute;
  left: 100%; /* Position at the end of the first column */
  top: -40px;
  height: 420px; /* Full height of the container */
  border-left: 2px solid var(--text-color); /* White vertical line */
}

.column {
  position: relative; /* For positioning the vertical line */
}

.timeline {
  margin-bottom: 2rem; /* Adjust the space as needed */
}

.date {
  font-size: 30px;
  margin-bottom: 12px;
}

.dateSubheader {
  font-size: 14px;
}

@media (max-width: 829px) {
  .verticalLine {
    display: none;
  }
  .gridContainer {
    grid-template-columns: 1fr;
  }
  .date,
  .dateSubheader {
    display: none;
  }
  .gridContainer {
    gap: 0;
  }
}

